body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.searchbox {
	text-align: center;
	width: 1250px;

}

#searchInputField {
	padding: 0 10px;
	height: 50px;
	width: 73%;
	font-size: 38px;
}

#searchInputBtn {
	height: 55px;
	width: 55px;
	margin-left: 5px;
	font-size: 38px;
	color: white;
	background-color: #28955D;
	border: 0px;
	border-radius: 3px;
	transform: scaleX(-1);
    -moz-transform: scaleX(-1);
    -webkit-transform: scaleX(-1);
    -ms-transform: scaleX(-1);
}
.item {
	display: grid;
	grid-template-columns: 2fr 3fr;
	grid-gap: 50px;
	text-align: left;
	max-width: 80%;
	margin: 0 auto;
}

.push-right {
	padding-right: 15px;
}


.App {
  font-family: 'Roboto', sans-serif;
}

.center {
  text-align: center;
}

.left {
	text-align: left;
}

.banner {
  background-image: -webkit-linear-gradient(left, #235A92, #28955D);
  background-image: linear-gradient(to right, #235A92, #28955D);
  color: #fff;
  height: 125px;
  line-height: 125px;
  font-size: 2.5em;
}

.container {
	max-width: 1250px;
	margin: 0 auto;
}

.ugh-header {
	padding-left: 125px;
	color: #28955D;
}
